.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.blank{
  color: rgb(38, 32, 215);
}
.box{
  margin: 10px;
  box-sizing: content-box;
  display: flex;
  width:auto;
  height: auto;
  border: 2px solid black;
  border-radius: 5px;
  color:rgb(5, 4, 5);
  background-color: rgb(222, 186, 142,0.24);
}
body{
  background: #e3eae3;
  z-index: 3;
}

